<template>
  <div class="standardSupplies h100">
    <el-row class="h100">
      <el-col :span="6" class="pd-r10 h100">
        <assetsTree
          :treeSetting="deptTreeSetting"
          @handleClickTree="handleClickTree"
          @save="saveAssetsType"
          @delete="deleteAssetsType"
          :haveRemark="true"
          :hideBtn="hideBtn"
          ref="orgTree"
        ></assetsTree>
      </el-col>
      <el-col :span="18" class="h100">
        <KlTable v-bind="tableConfig" ref="klTable"></KlTable>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import {
  getListApi, //获取资产分类的列表
  getAssetTypeTree, //获取资产分类的分类树
  AddAssetType, //资产分类添加分类
  getTypeSuperList, //-查询资产分类上级
  deleteAssetType, //--删除资产分类
  queryTypeList, //--资产分类列表
  updateAssetType //--更新资产分类
} from "@/api/itAssets/assetClassification";
import assetsTree from "../assetsTree";
export default {
  components: { assetsTree },
  data() {
    return {
      // 组织树
      deptTreeSetting: {
        title: "资产分类",
        select: true,
        btnArr: ["add"],
        defaultProps: {
          children: "children",
          label: "name"
        }
      },
      //物资信息
      tableConfig: {
        showFixedHeight: true,
        searchObj: {
          searchMethod: (params, that) => {
            if (this.tableConfig.tableDataObj) {
              this.getTableData();
            } else {
            }
          },
          searchArr: [
            {
              type: "el-input",
              label: "资产类型",
              model: "typename",
              placeholder: "请输入资产类型名称"
            }
          ]
        },
        // 查询条件参数
        queryObj: {
          method: () => {
            this.getTableData();
          },
          queryData: {
            parentid: "",
            page: 1,
            pageSize: 15
          }
        },
        tableDataObj: {
          tableArr: [],
          total: null
        },
        operateArr: [
          {
            name: "新建",
            isHide: true,
            icon: "el-icon-plus",
            method: name => {
              this.toAdd();
            }
          }
        ],
        //  table表配置参数
        tableObj: {
          // 左边选择框展示
          selectionShow: false,
          // 左边序号展示
          indexShow: true,
          // table表事件参数配置
          tableMethods: {
            selection: val => {
              this.selectionData = val;
            }
          },
          // table表展示列配置
          showItems: [
            {
              name: "资产编码",
              prop: "typecode"
              // width: 80,
              // align: "left",
            },
            {
              name: "资产类型",
              prop: "typename",
              width: 100
            },
            {
              name: "排序",
              prop: "sort"
              // width: 150,
              // align: "left",
            }

            // {
            //   name: "父级名称",
            //   prop: "parentName",
            //   // align: "left",
            //   // width: 100,
            // },
          ]
        },
        // 分页展示总条配置
        paginationObj: {
          pageSizes: [15, 30, 50, 100]
        },
        paginationIsshow: true,
        showklTable: false
      },
      //当前选择树
      currentTreeNode: {},
      hideBtn: false
      // hideBtn: !this.$store.getters["isBtnAuth"]("insert")
    };
  },
  watch: {
    currentTreeNode(val) {
      // console.log(val);
      let id = val.id || "";
      //获取表格的信息
      this.getTableData(id);
    }
  },
  created() {
    this.getAssetsType();
  },
  methods: {
    //点击树
    handleClickTree(node) {
      this.currentTreeNode = Object.assign({}, node);
    },
    //获取资产分类树
    getAssetsType() {
      getAssetTypeTree().then(res => {
        this.$refs.orgTree.setTreeData(res.data.children);
      });
    },
    //保存\编辑资产类别
    saveAssetsType(data) {
      try{
        if (data.id) {
          //编辑
          updateAssetType(data).then(res => {
              this.getAssetsType();
          })
        } else {
          //新增
          AddAssetType(data).then(res => {
              this.getAssetsType();
          })
        }
        this.$message.success("保存成功！");
        this.$refs.orgTree.doClose();
      }catch(e){
          this.$message.success("保存失败！");
          console.log(e);
      }
    },
    //删除组织
    deleteAssetsType(id) {
      deleteAssetType({ id: id }).then(res => {
        this.getAssetsType();
        this.currentTreeNode = {};
        this.$message.success("删除成功！");
      });
    },
    //获取资产分类列表信息
    getTableData(id) {
      let form = Object.assign({}, this.tableConfig.queryObj.queryData);
      form.parentid = id;
      form.pageCount = form.pageSize;
      if (form.parentid === undefined) {
        form.parentid = "";
      }
      this.$delete(form, "pageSize");
      getListApi(form)
        .then(res => {
          // console.log(res.data);
          this.$set(
            this.tableConfig.tableDataObj,
            "tableArr",
            res.data.list || []
          );
          this.$set(
            this.tableConfig.tableDataObj,
            "total",
            res.data.total || 0
          );
        })
        .catch(res => {
          this.$set(this.tableConfig.tableDataObj, "tableArr", []);
          this.$set(this.tableConfig.tableDataObj, "total", 0);
        });
    }
  }
};
</script>

<style lang="scss">
.standardSupplies {
  padding-top: 10px;
  .pd-r10 {
    padding-right: 10px;
  }
}
</style>