<template>
  <div class="assetsTree">
    <div class="edit-tree-toolbar">
      <div>{{title}}</div>
      <div class="edit-btn" v-if="!hideBtn">
        <template v-for="(i,index) in btnConfig">
          <el-button
            class="btn-text-blue"
            size="mini"
            :key="index"
            v-if="showBtn(i.name)"
            :icon="i.icon"
            @click="i.methons"
          >{{i.text}}</el-button>
        </template>
      </div>
    </div>
    <div class="edit-tree-content">
      <el-tree
        class="filter-tree"
        :data="treeConfig.data"
        node-key="id"
        :props="defaultProps"
        :expand-on-click-node="clickExpand"
        default-expand-all
        highlight-current
        @node-click="treeNodeClick"
        ref="tree"
      >
        <span class="custom-tree-node" slot-scope="{ node,data }">
          <span>{{ node.label }}</span>
          <span v-if="currentNode.id===data.id&&!hideBtn&&data.id!==-1">
            <el-button
              type="text"
              @click.native="()=>TNodeAdd(node,data)"
              class="btnIcon"
              icon="el-icon-plus"
            ></el-button>
            <el-button
              type="text"
              @click.native="()=>TNodeEdit(node,data)"
              class="btnIcon"
              icon="el-icon-edit-outline"
            ></el-button>
            <el-button
              type="text"
              @click.native="()=>TNodeDel(node,data)"
              class="btnIcon"
              icon="el-icon-delete"
            ></el-button>
          </span>
        </span>
      </el-tree>
    </div>
    <dialogPublic
      :title="title"
      :show.sync="show"
      v-if="show"
      @doSubmit="doSubmit"
      @doClose="doClose"
    >
      <editForm :formConfig="formConfig" ref="editForm" :formData="editFormData"></editForm>
    </dialogPublic>
  </div>
</template>
<script>
import {
  getListApi, //获取资产分类的列表
} from "@/api/itAssets/assetClassification";
export default {
  props: ["treeSetting", "haveRemark", "hideBtn"],

  data() {
    const data = [
      {
        id: 1,
        label: "一级 1",
        children: [
          {
            id: 4,
            label: "二级 1-1",
            children: [
              {
                id: 9,
                label: "三级 1-1-1"
              },
              {
                id: 10,
                label: "三级 1-1-2"
              }
            ]
          }
        ]
      },
      {
        id: 2,
        label: "一级 2",
        children: [
          {
            id: 5,
            label: "二级 2-1"
          },
          {
            id: 6,
            label: "二级 2-2"
          }
        ]
      },
      {
        id: 3,
        label: "一级 3",
        children: [
          {
            id: 7,
            label: "二级 3-1"
          },
          {
            id: 8,
            label: "二级 3-2"
          }
        ]
      }
    ];
    return {
      title: "树标题",
      treeConfig: {
        select: true,
        data: JSON.parse(JSON.stringify(data))
      },
      btnConfig: [
        {
          name: "add",
          text: "根节点",
          icon: "el-icon-plus",
          methons: () => {
            this.add();
          }
        },
        {
          name: "edit",
          text: "编辑",
          icon: "el-icon-edit",
          methons: () => {
            this.edit();
          }
        },
        {
          name: "delete",
          text: "删除",
          icon: "el-icon-delete",
          methons: () => {
            this.delete();
          }
        }
      ],
      btnArr: [],
      defaultProps: {
        children: "children",
        label: "orgName"
      },
      currentNode: {}, //当前节点
      show: false,
      formConfig: {
        itemArr: [
          {
            label: "父节点",
            prop: "parentName",
            type: "text",
            disabled: true,
            span: 24
          },
          {
            label: "节点名称",
            prop: "typename",
            type: "text",
            span: 24,
            required: true
          },
          {
            label: "节点编码",
            prop: "typecode",
            type: "text",
            span: 24,
            required: true
          },
          {
            label: "排序",
            prop: "sort",
            type: "number",
            span: 24,
            required: true
          }
        ]
      },
      editFormData: {},
      remarkItem: {
        label: "备注",
        prop: "remark",
        type: "text",
        span: 24
      }
    };
  },
  created() {
    this.init();
  },
  watch: {
    treeSetting: {
      handler(val) {
        this.$set(this.treeConfig, "data", val.data || []);
      },
      deep: true
    }
  },
  methods: {
    init() {
      let { btnArr, title, data, defaultProps, clickExpand } = this.treeSetting;
      this.btnArr = btnArr || [];
      this.clickExpand = clickExpand || false; //是否点击节点展开
      this.defaultProps = Object.assign({}, this.defaultProps, defaultProps);
      this.title = title || "";
      this.$set(this.treeConfig, "data", data || []);
      if (this.haveRemark) {
        this.formConfig.itemArr.push(this.remarkItem);
      }
    },
    //显示按钮
    showBtn(btn) {
      return this.btnArr.indexOf(btn) != -1;
    },
    //点击新增
    
    add() {
      this.show = true;
    },
    //点击编辑
    edit(node) {},
    //点击删除
    delete() {},
    //返回树信息
    getFormData() {
      let { data } = this.treeConfig;
      data.map(i => delete i.rowIndex);
      return data;
    },
    //点击树
    treeNodeClick(node) {
      this.currentNode = JSON.parse(JSON.stringify(node));
      this.$emit("handleClickTree", node);
    },
    //确定
    doSubmit() {
      this.$refs.editForm.getFormData().then(res => {
        this.$emit("save", res);
      });
    },
    //关闭
    doClose() {
      this.show = false;
    },
    //新增字节点
    TNodeAdd(node, data) {
      this.show = true;
      let { id, name, parentId, classcode} = data;
      // this.editFormData = {
      //   orgName: "",
      //   parentId: id,
      //   parentName: orgName
      // };
      this.editFormData = {
        parentcode: classcode,
        parentid: id,
        // parentName: name
      };
    },
    //编辑
    TNodeEdit(node, data) {
      let form = {
        typename: data.name
      }
      getListApi(form).then(res=>{
        let formData=res.data.list[0];
        this.editFormData = Object.assign({}, formData);
      })
      this.show = true; 
    },
    //删除
    TNodeDel(node, data) {
      let name = 
      this.$confirm("此操作将删除【" + data.orgName + "】, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.$emit("delete", data.id);
        })
        .catch(e => {});
    },
    setNoNode() {
      this.$refs.tree.setCurrentKey(null);
      this.currentNode = {};
    },
    getCurrentKey() {
      return this.$refs.tree.getCurrentKey();
    },
    //设置树数据
    setTreeData(data) {
      this.$set(this.treeConfig, "data", data);
      setTimeout(() => {
        let node = {};
        if (data.length) {
          let dataArr = this.$utils.convert_tree_data(data);
          let obj = dataArr.find(i => i.id == this.currentNode.id);//查找当前选择的节点所在的位置
          node = obj ? this.currentNode : data[0];//如果已有选中的节点，则显示已选择的节点，没有则默认显示第一个节点
        }
        this.$refs.tree.setCurrentKey(node.id);//设置当前选择的节点
        this.treeNodeClick(node);
      }, 0);
    },
    //关闭弹窗
    choseDialog() {
      this.show = false;
    }
  }
};
</script>
<style>
.assetsTree
  .el-tree--highlight-current
  .el-tree-node.is-current
  > .el-tree-node__content {
  background-color: #bedaf9 !important;
}
</style>
<style lang="scss" scoped>
.assetsTree {
  border: 1px solid #EBEEF5;
  min-height: 300px;
  max-height: 600px;
  overflow-y: auto;
  overflow-x: auto;
  margin-bottom: 10px;
}

.edit-tree-toolbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
}
.custom-tree-node {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 14px;
  padding-right: 8px;
}
</style>